$borderR : 10px;
$photoSize : 200px;
$pad : 10px;
$contNormalW: 1200px;
$contSmallW: 768px;
$contExtraSmallW: 480px;

div.registryCont{
  display: flex;
  justify-content: space-between;
  background-color: rgba(179, 179, 179, 0.712);
  border-radius: $borderR;
  padding: $pad;
  @media screen and (max-width: $contExtraSmallW){
    flex-direction: column;
    align-items: center;
  }
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.322);
}

div.registryData{
  *{
    padding: 0 0 $pad 0;
    @media screen and (max-width: $contExtraSmallW){
      text-align: center;
    }
  }
}

div.imgSpCont{
  flex-shrink: 0;
  width: $photoSize;
  height: $photoSize;
  padding: 0 0 0 $pad;
  border-radius: $borderR;
  @media screen and (max-width: $contExtraSmallW){
    padding: $pad 0 0 0;
  }
}

img.imgSp{
  border-radius: $borderR;
  object-fit: cover;
  width:100%; 
  height:100%;
}